export function autoResponse(navItem, inputText){
    let obj = {}
    // 
    if(navItem.type==''){
        if(inputText==''){
            obj.text = 'HiU~'
            // obj.linkList = ['','','','']
        }
    } else if(navItem.type==''){
        if(inputText==''){
            obj.text = 'HiU~'
            // obj.linkList = ['','']
        }
    } else if(navItem.type==''){
        if(inputText==''){
            obj.text = 'HiU~'
            // obj.linkList = ['','999']
        }
    } else if(navItem.type==''){
        if(inputText==''){
            obj.text = '~'
            obj.linkList = [
                {
                    name: '',
                    path: ''
                },{
                    name: '',
                    path: '/feedback'
                }
        ]}
    }else{
        // 
        if(inputText.indexOf('')>-1){
            obj.text = ''
        }else if(inputText.indexOf('')>-1){
            obj.diseaseList = ['','',''],
            obj.departmentList = ['']
        }else if(inputText.indexOf('')>-1){
            obj.diseaseInfo = {
                title: '',
                detail: ' ......',
                cause: ''
            }
            obj.diseaseList = ['','',''],
            obj.departmentList = ['']
        }else if(inputText.indexOf('')>-1){
            obj.text = 'sorry, '
        }else if(inputText.indexOf('')>-1){
            obj.text = ''
            obj.linkList = ['','']
        }else if(inputText.indexOf('999')>-1){
            obj.text = ''
            obj.linkList = ['','']
        }else{
            obj.text = 'sorry, '
        }
    }
    return obj
}


